	//   	color: #fff;
	//   	line-height: 20px;
	//   	height: 20px;
	//   	padding: 0 10px;
	//   	margin-left: 5px;
	//   	left: 100%;
	//   	top: 0;
	// }
	.graph__bar__each:hover:after {
	  	display: block;
	}

	.graph__legend {
	  	position: absolute;
	  	margin-right: 10px;
	  	font-size: 1em;
	  	top: 3px;
	  	left: -50px;
	  	z-index: 9999;
	}
/* end progress bar */