@media only screen and (device-width:768px) and (device-height:1024px) and (-webkit-min-device-pixel-ratio:1) {
	// ========================== LIVE B2C ======================= //
	.header__profpic {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.header__title {
		flex: 4;
		color: $secondary;
		font-size: 1.5em;
		margin: 0;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.header__mobile {
		flex: 0 64px;
		background-color: transparent;
		display: flex;
		height: 60px;
	}

	.header__desktop {
		display: none;
	}

	.main__sidebar {
		display: none;
	}

	.mobile__menu {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.dashboard,
	.profile {
		width: 95%;

		&__menu {
			justify-content: space-between;
			flex-wrap: wrap;

			div {
				height: 120px;
				margin-bottom: 10px;

				img {
					width: 70px;
				}

			}

		}

		&__menutab {
			.boxsessions {
				margin-top: 30px;

				&__today {
					.boxinfo {
						left: 0px !important;
						width: 95% !important;
						margin-left: 5px;
					}

				}

			}

		}

		.dashboard__resultbook {
			.profilecoach {
				width: 25%;
			}

		}

	}

	// ==============================++++++++ BOOK A COACH +++++++========================//

	.dashboard__bookacoach {
		&__searchname {
			input {
				width: 70% !important;
			}

		}

		.date-picker .ui-datepicker-header {
			font-size: 0.9em;
		}

		.date-picker .input .result {
			font-size: 0.8em !important;
			letter-spacing: -1.5px !important;
		}

		.date-picker .input button {
				width: 10px !important;
			

		}

	}

	.dashboard__resultbook {
		padding: 0 30px;
		justify-content: space-between!important;
		.boxprofilecoach {
			width: 270px !important;

			.profilecoach__timebook {
				.accordion-item {
					width: 100% !important;
				}

				/* Thumb */

				/* Panel */
				.accordion-panel {
					width: 100% !important;
				}

			}

		}

	}

}

