	// ==============================++++++++  Booking Coach for Mobile +++++++========================//

	.dashboard__bookacoach {
		display: none;
	}

	.dashboard__bookacoachmobile {
		margin-top: 10px;
		display: flex !important;
		flex-direction: column;
		width: 100%;

		.boxselect {
			display: flex !important;
			border: 2px solid #606983;
			height: 70px;
			background-color: rgba(56, 70, 112, .98);

			select {
				width: 60%;
				height: 35px;
				font-size: 1em;
				color: $white;
				margin: auto;
				letter-spacing: 1px;
				text-indent: 50px;
			}

		}

		#alt1 {
			margin-top: 20px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			border: 2px solid #606983;
			background-color: rgba(56, 70, 112, .98);

			h1 {
				margin-top: 5px;
				font-size: 1.2em;
				margin-bottom: 10px;
			}

			input {
				height: 30px;
				width: 85%;
			}

			button {
				margin-bottom: 20px;
				min-width: 100px;
				border-radius: 3px;
			}

		}

		#alt2 {
			margin-top: 20px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			border: 2px solid #606983;
			background-color: rgba(56, 70, 112, .98);
			margin-bottom: 20px;

			date-picker {
				margin: 200px auto;
			}

			h1 {
				margin-top: 5px;
				font-size: 1.2em;
				margin-bottom: 10px;
				order: 1;
			}

			.btnsearch {
				order: 3;
				margin-bottom: 20px;

				button {
					min-width: 100px;
					border-radius: 3px;
				}

			}

			.date-picker {
				order: 2;
				width: 260px;
				height: auto;
				max-height: 30px;
				background-color: rgba(56, 70, 112, .98);
				position: relative;
				overflow: hidden;
				-webkit-transition: all 0.3s 0s ease-in-out;
				transition: all 0.3s 0s ease-in-out;
				border-radius: 3px;
			}

			.date-picker .input {
				width: 100%;
				height: 30px;
				font-size: 0;
				cursor: pointer;
				border-radius: 3px;
				background-color: white;
			}

			.date-picker .input .result,
			.date-picker .input button {
				display: inline-block;
				vertical-align: top;
			}

			.date-picker .input .result {
				width: calc(100% - 46px);
				height: 30px;
				line-height: 30px;
				font-size: 13px;
				padding: 0 10px;
				color: grey;
				box-sizing: border-box;

				span {
					color: black;
					font-weight: bold;
				}

			}

			.date-picker .input button {
				width: 30px;
				height: 30px;
				background-color: #fff;
				color: white;
				line-height: 30px;
				border: 0;
				font-weight: bold;
				font-size: 16px;
				padding: 0;
				position: relative;
				left: 10px;
				cursor: pointer;

				.fa-angle-down {
					color: gray;
				}

			}

			.date-picker .input button:hover {
				background-color: #fff;
			}

			.date-picker .input button:focus {
				outline: none;
			}

			.date-picker .calendar {
				position: relative;
				width: 100%;
				background: #fff;
				border-radius: 3px;
				overflow: hidden;
				margin-top: 15px;
			}

			.date-picker.open {
				max-height: 400px;
			}

			.date-picker .ui-datepicker-inline {
				position: relative;
				width: 100%;
			}

			.date-picker .ui-datepicker-header {
				height: 100%;
				line-height: 50px;
				background: #8392A7;
				color: #fff;
				margin-bottom: 10px;
			}

			.date-picker .ui-datepicker-prev,
			.date-picker .ui-datepicker-next {
				width: 20px;
				height: 20px;
				text-indent: 9999px;
				border: 2px solid #fff;
				border-radius: 100%;
				cursor: pointer;
				overflow: hidden;
				margin-top: 12px;
			}

			.date-picker .ui-datepicker-prev {
				float: left;
				margin-left: 12px;
			}

			.date-picker .ui-datepicker-prev:after {
				-webkit-transform: rotate(45deg);
				transform: rotate(45deg);
				margin: -43px 0px 0px 8px;
			}

			.date-picker .ui-datepicker-next {
				float: right;
				margin-right: 12px;
			}

			.date-picker .ui-datepicker-next:after {
				-webkit-transform: rotate(-135deg);
				transform: rotate(-135deg);
				margin: -43px 0px 0px 6px;
			}

			.date-picker .ui-datepicker-prev:after,
			.date-picker .ui-datepicker-next:after {
				content: "";
				position: absolute;
				display: block;
				width: 4px;
				height: 4px;
				border-left: 2px solid #fff;
				border-bottom: 2px solid #fff;
			}

			.date-picker .ui-datepicker-prev:hover,
			.date-picker .ui-datepicker-next:hover,
			.date-picker .ui-datepicker-prev:hover:after,
			.date-picker .ui-datepicker-next:hover:after {
				border-color: #68768A;
			}

			.date-picker .ui-datepicker-title {
				text-align: center;

				span {
				}

			}

			.date-picker .ui-datepicker-calendar {
				width: 100%;
				text-align: center;
			}

			.date-picker .ui-datepicker-calendar thead tr th span {
				display: block;
				width: 100%;
				color: #37456f;
				margin-bottom: 5px;
				font-size: 15px;
				font-weight: bold;
			}

			.date-picker .ui-state-default {
				display: block;
				text-decoration: none;
				color: #b5b5b5;
				line-height: 34px;
				font-size: 14px;
			}

			.date-picker .ui-state-default:hover {
				background: #49c5fe;
			}

			.date-picker .ui-state-highlight {
				color: #344269;
			}

			.date-picker .ui-state-active {
				color: #344269;
				background-color: rgba(73, 197, 254, 0.49);
				font-weight: 600;
			}

			.date-picker .ui-datepicker-unselectable .ui-state-default {
				color: #344269;
				border: 2px solid transparent;
			}

			.date-picker.open .input button {
				background: #fff;
			}

		}

