					}

					span {
						font-size: 0.9em !important;
					}

				}

				&__boxstatus {
					height: 140px !important;

					.daterefund {
						i {
							font-size: 0.7em !important;
						}

						span {
							font-size: 0.9em !important;
						}

					}

					.status {
						margin-top: 30px !important;
					}

				}

				&__boxsession {
					i {
						font-size: 0.7em !important;
						color: #a3aabe;
					}

					span {
						font-size: 0.9em !important;
					}

				}

			}

		}

	}

